@import url(https://fonts.googleapis.com/css?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css?family=League+Script&display=swap);
/* font-family: 'Playfair Display', serif;
font-family: 'Pacifico', cursive;
font-family: 'Poiret One', cursive; */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  background-image: url("https://image.freepik.com/free-photo/old-book-open-dark-wood-background_1373-581.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  font-family: 'League Script', cursive; 
  display: flex;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#ori-warning {
  display: flex;
}

.orientation {
  font-family: 'Playfair Display', serif;
  width: 25vw;
  color: rgba(4,20,3,0.7);
  font-weight: bold;
  margin-top: 45vh;
  font-size: 5vw;
  z-index: 500;
  text-align: center;
}

#orientation-left {
  margin-left: 20vw
}

#spacer {
  width: 10vw;
}

@media (orientation: portrait) {
  .orientation { display: block; }
  .interaction  { display: none; }
  }

@media (orientation: landscape) {
  .orientation  { display: none; }
  .interaction { display: block; }
}

#modal {
  display: flex;
  align-content: center;
}
/* @import '~pretty-checkbox/src/pretty-checkbox.scss'; */

#craftboard {
  display: flex;
  height: 100vh;
  width: 100vw;
  font-size: calc(20px + 1vmin);
  color: rgb(19, 36, 19);
}

#rsvp {
  font-size: 3vh;
  z-index: 2;
  bottom: 0;
  right: 0;
  position: absolute;
  width: 71vw;
  height: 65vh;
  color: rgb(19, 36, 19);
  font-weight: bold;
  text-align: center;
}

.saveTheDate {
  height: 10vh;
  background-color: rgba(0,0,0,0);
  -webkit-transform: rotate(-7deg);
          transform: rotate(-7deg);
  border: none;
}

.saveTheDateClicked {
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
  opacity: 0.7;
  height: 10vh;
  background-color: rgba(0,0,0,0);
  -webkit-transform: rotate(-7deg);
          transform: rotate(-7deg);
  border: none;
}

#name-input {
  background-color: rgba(0,0,0,0);
  border: none;
  border-bottom: #61dafb 1px dashed;
  font-family: 'League Script', cursive;
  font-size: 1em;
  text-align: right;
  font-weight: bold;
}

::-webkit-input-placeholder { /* Firefox, Chrome, Opera */ 
    color: rgba(27, 24, 24, 0.4);
    font-weight: bold;
}

::-moz-placeholder { /* Firefox, Chrome, Opera */ 
    color: rgba(27, 24, 24, 0.4);
    font-weight: bold;
}

:-ms-input-placeholder { /* Firefox, Chrome, Opera */ 
    color: rgba(27, 24, 24, 0.4);
    font-weight: bold;
}

::-ms-input-placeholder { /* Firefox, Chrome, Opera */ 
    color: rgba(27, 24, 24, 0.4);
    font-weight: bold;
}

::placeholder { /* Firefox, Chrome, Opera */ 
    color: rgba(27, 24, 24, 0.4);
    font-weight: bold;
} 
  
:-ms-input-placeholder { /* Internet Explorer 10-11 */ 
    color: rgba(27, 24, 24, 0.4);
    font-weight: bold;
} 
  
::-ms-input-placeholder { /* Microsoft Edge */ 
    color: rgba(27, 24, 24, 0.4); 
    font-weight: bold;
} 

input:focus { 
  outline: none;
}


input[type=checkbox] {
  width: 30px;
  height: 30px;
  margin-right: 8px;
  cursor: pointer;
  font-size: 17px;
  visibility: hidden;
}

input[type=checkbox]:after {
  content: " ";
  background-color: rgba(10,250,10,0.5);
  display: inline-block;
  margin-left: 10px;
  padding-bottom: 5px;
  color: rgba(27, 24, 24, 0.7);
  width: 1.5vw;
  height: 1vw;
  visibility: visible;
  /* border: 1px solid #; */
  padding-left: 3px;
  /* border-radius: 5px; */
  -webkit-transform: rotate(-6deg);
          transform: rotate(-6deg);
}

input[type=checkbox]:checked:after {
  content: "\2714";
  padding: -5px;
  font-weight: bold;
}

.interaction {
  z-index: 5;
}

